import Script from 'next/script'
import SideNav from "@/components/parts/SideNav/SideNav"
import Breadcrumb from "@/components/parts/Breadcrumb/Breadcrumb"
import AuthorProfile from "@/components/pages/insightsDetail/AuthorProfile/AuthorProfile"
import Series from "@/components/pages/insightsDetail/Series/Series"
import RelArticles from "@/components/parts/RelArticles/RelArticles"
import Contact from "@/components/parts/Contact/Contact"
import DetailTop from "@/components/parts/PageHead/DetailTop"
import Article from "@/components/parts/DetailPage/Article/Article"
import FootSlider from "@/components/parts/FootSlider/FootSlider"
import getInsightsStatic from '@/fetch/static/getInsightsStatic'
import getInsightsSeriesDetail from '@/fetch/static/getInsightsSeriesDetail'
import { GlossaryType } from '@/types/zodType'
import { CardType } from '@/types/contentsType'
import { KeywordType } from '@/types/detailPages'

export async function generateStaticParams() {
  const { list } = await getInsightsStatic({ all: true })
  const paramSlug = list.map((item: CardType) => ({
    slug: item.slug
  }))

  return paramSlug
}

export async function generateMetadata({ params }: { params: { slug: string }}) {
  const { slug } = await params
  const { details } = await getInsightsSeriesDetail(slug)

  return {
    title: `${details.subject}`,
    description: `${details.lead_text}`,
  }
}

export default async function Page({ params }: { params: { slug: string }}) {
  const { slug } = await params
  const { details, category } = await getInsightsSeriesDetail(slug)
  const { list } = await getInsightsStatic({ all: true })

  const breadcrumb = [
  {
    title:  'コラム',
    href: '/insights/'
  },
  {
    title: details.subject
  }
]

  const jsonLdBreadcrumb = {
    '@context': 'https://schema.org',
    '@type': 'BreadcrumbList',
    itemListElement: [
      {
        '@type': 'ListItem',
        position: 1,
        name: 'ホーム',
        item: 'https://www.tourism.jp/'
      },
      {
        '@type': 'ListItem',
        position: 2,
        name: 'コラム',
        item: 'https://www.tourism.jp/insights/'
      },
      {
        '@type': 'ListItem',
        position: 3,
        name: details.subject,
        item: `https://www.tourism.jp/insights/detail/${details.slug}`
      }
    ]
  }

  // head
  const head = {
    title: details.subject,
    release: details.ymd,
    update: details.update_ymdhi.split('T')[0],
    // download: 'https://tourism.g.kuroco-img.app/v=1766049351/files/topics/4_ext_8_0.pdf',
    login: true,
    author: {
      name: details.author_external_name,
      // img: details,
      text: details.author_external_title
    }
  }

  // nav
  const keywords = details.related_glossary.map((glossary: GlossaryType) => {
    return { name: glossary.subject, url: glossary.slug }
  }) as KeywordType[]

  const parser = new DOMParser()
  const doc = parser.parseFromString(details.contents_default, 'text/html')
  const sections = Array.from(
    doc.querySelectorAll('h2'),
    (h2) => h2.textContent?.trim() ?? ''
  )

  const nav = {sections, keywords, numbering: details.index_numbering}

  // Series
  const seriesItems = list.filter((item: CardType) => {
    return item.contents_type_slug === details.contents_type_slug
  }).slice(5)

  return (
    <>
      <Breadcrumb data={breadcrumb} />

      <SideNav offset={50}>
        <DetailTop
          title={details.subject}
        />
        <Article head={head} nav={nav} />
        <AuthorProfile name={details.author_external_name} title={details.author_external_title} profile={details.author_external_profile} />
        <RelArticles list={details.related_posts} />
        <Series list={seriesItems} series_logo={category.series_logo.url} subject={category.subject} lead_text={category.lead_text} />
        <Contact />
        <FootSlider list={list.slice(5)} />
      </SideNav>
      <Breadcrumb data={breadcrumb} footer />

      <Script
          id="jsonld-breadcrumbList"
          type="application/ld+json"
          dangerouslySetInnerHTML={{
            __html: JSON.stringify(jsonLdBreadcrumb).replace(/</g, '\\u003c'),
          }}
        />
    </>
  )
}